*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  background-color: #ffb3a7;
}

.app {
  border-radius: 20px;
  background-color: #fff;
  max-width: 900px;
  margin: 10px auto;
  padding: 15px;
}

.shop-list {
  padding-top: 30px;
}

.navigation,
.shop-list__list,
.cart-list {
  display: flex;
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;

  @media screen and (max-width: 516px) {
    flex-wrap: wrap;
  }
}
.cart-list {
  display: block;
}

.shop-list-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  min-height: 100%;
  padding: 1.5rem 2rem;
  margin-bottom: 0.8rem;
  margin-right: 0.4rem;
  margin-left: 0.4rem;
}

.shop-list-item__image {
  display: block;
}

.shop-list-item__url {
  width: 100%;
}

.shop-list-item__name {
  font-weight: 800;
  font-size: 22px;
  color: #000;
  margin-bottom: 0;
  text-align: center;
}
.shop-list-item__description {
  font-weight: 400;
  font-size: 14px;
  line-height: 12px;
  color: #000;
  text-align: justify;
}
.shop-list-item__price {
  width: 100%;
  display: block;
  font-weight: 800;
  font-size: 22px;
  text-align: center;
}
.shop-list-item__name {
  padding-top: 5px;
  text-align: center;
}

.shop-list-item__price {
  padding-top: 10px;
}

.button {
  cursor: pointer;
  position: relative;
  margin-top: 10px;
  width: 100%;
  padding: 9px 0;
  background-color: transparent;
  border: 1px solid #c93756;
  border-radius: 3rem;
  color: #c93756;
  font-size: 16px;
  font-weight: 600;
}
.button:hover {
  background-color: #c93756;
  color: #fff;
}
.delete {
  width: 9em;
}

.navigation {
  padding: 10px 10px;
  justify-content: space-between;
}

.navigation__item {
  position: relative;
  text-transform: uppercase;
  font-size: 25px;
  cursor: pointer;
}

.navigation__item:not(:last-child) {
  margin-right: 10px;
}

a {
  text-decoration: none;
}
a {
  color: black;
}

.active {
  color: #c93756;
}

.cart-list-item {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 5px;
  justify-content: space-between;
  margin-bottom: 10px;
}
.cart-list-item__header {
  display: flex;
  align-items: center;
}
.cart-list-item__image {
  width: 50px;
  height: 50px;
}
.cart-list-item__image img {
  width: 100%;
}

.cart-list-item__name {
  padding-left: 10px;
}
.button-cart {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background-color: #ffb3a7;
  border: none;
  font-size: 25px;
}
.cart-list-item__count {
  padding: 0 5px;
  font-size: 20px;
  font-weight: 700;
}
.button-cart {
  cursor: pointer;
}
.cart-list-item__total-price {
  text-align: center;
  font-size: 20px;
  font-weight: 700;
}
.cart__total-price {
  text-align: right;
  color: #c93756;
  padding: 5px;
  font-size: 24px;
  font-weight: 600;
  font-style: italic;
  text-decoration: underline;
}
.cart__total-price-number {
  color: #000;
  font-size: 26px;
  font-weight: 700;
}
.header__empty-cart {
  text-align: center;
  font-size: 40px;
  font-weight: 700;
  color: #c93756;
}
